// https://stackoverflow.com/a/25984542
export const shuffleArray = (array) => {
    let count = array.length;
    let randomnumber;
    let temp;
    while (count) {
        randomnumber = Math.random() * count-- | 0;
        temp = array[count];
        array[count] = array[randomnumber];
        array[randomnumber] = temp;
    }
};
